.btn:hover {
  transition: opacity .25s ease-in-out;
  -moz-transition: opacity .25s ease-in-out;
  -webkit-transition: opacity .25s ease-in-out;
}

.btn-not-first {
  margin-left: 1em;
}

@media (max-width: 767px) {
  .btn {
    margin-bottom: 1em;
  }
  .btn-not-first {
    margin-left: 0;
  }
}

.btn-lg {
  padding: 10px 35px;
}

.pull-center {
  text-align: center;
}

.btn {
  border-radius: 30px;
  border-width: 1px;
  word-wrap: break-word !important;
  white-space: normal !important;
}

.action-buttons .btn {
  margin-top: 0;
}

