}
*/
.btn-transparent-white {
  background-color: transparent;
  border-color: #ffffff;
  color: #ffffff;
}

.btn-transparent-primary {
  background-color: transparent;
  border-color: #222;
  color: #222;
}

.btn-transparent-secondary {
  background-color: transparent;
  border-color: #666;
  color: #666;
}

.btn-transparent-white:hover, .btn-transparent-primary:hover, .btn-transparent-secondary:hover {
  background-color: rgba(255, 255, 255, 0.15) !important;
}

.btn-transparent-white:hover {
  color: #ffffff !important;
}

.btn-transparent-primary:hover {
  color: #222 !important;
}

.btn-transparent-secondary:hover {
  color: #666 !important;
}

.btn-primary-on-white {
  background-color: #ffffff;
  color: #222 !important;
  border-color: #ffffff;
}

.btn-secondary-on-white {
  background-color: #ffffff;
  color: #666;
  border-color: #ffffff;
}

.btn-primary-on-white:hover {
  background-color: #d9d9d9;
  border-color: #d9d9d9;
  color: #222;
}

.btn-secondary-on-white {
  background-color: #f9f9f9;
  color: #666;
}

.btn-white-on-primary {
  background-color: #222;
  color: #ffffff;
  border-color: #222;
}

.btn-white-on-secondary {
  background-color: #666;
  color: #ffffff;
  border-color: #666;
}

.btn-white-on-primary:hover {
  background-color: black;
  border-color: black;
  color: #ffffff;
}

.btn-white-on-secondary:hover {
  background-color: #404040;
  border-color: #404040;
  color: #ffffff;
}

.btn-text-only-primary {
  background-color: transparent;
  border: none;
  color: #222;
}

.btn-text-only-secondary {
  background-color: transparent;
  border: none;
  color: #666;
}

.btn-text-only-white {
  background-color: transparent;
  border: none;
  color: #ffffff;
}

.btn-text-only-primary:hover {
  text-decoration: underline;
  color: #222;
}

.btn-text-only-secondary:hover {
  text-decoration: underline;
  color: #666;
}

.btn-text-only-white:hover {
  text-decoration: underline;
  color: #ffffff;
}
